:host {
  display: block;
}

.chat-summary-header {
  background: var(
    --color-primary-color,
    linear-gradient(135deg, #1e40af 0%, #3b82f6 100%)
  );
  color: var(--color-primary-contrast, #ffffff);
  padding: 0.75rem 1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.header-content {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.header-title {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 600;
  font-size: 1rem;
  white-space: nowrap;

  i {
    font-size: 1.125rem;
  }
}

.summary-items {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex: 1;
  flex-wrap: wrap;
}

.summary-item {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  padding: 0.25rem 0.625rem;
  background-color: rgba(255, 255, 255, 0.15);
  border-radius: 9999px;
  font-size: 0.75rem;
  white-space: nowrap;
  border: none;
  color: inherit;
  font-family: inherit;

  i {
    font-size: 0.75rem;
    opacity: 0.9;
  }

  .summary-value {
    font-weight: 500;
  }

  .edit-icon {
    font-size: 0.625rem;
    opacity: 0;
    transition: opacity 0.2s ease;
    margin-left: 0.125rem;
  }

  &.empty {
    opacity: 0.6;
    background-color: rgba(255, 255, 255, 0.08);
  }

  &.clickable {
    cursor: pointer;
    transition:
      background-color 0.2s ease,
      transform 0.15s ease;

    &:hover {
      background-color: rgba(255, 255, 255, 0.25);

      .edit-icon {
        opacity: 1;
      }
    }

    &:active {
      transform: scale(0.97);
    }

    &:focus {
      outline: 2px solid rgba(255, 255, 255, 0.5);
      outline-offset: 2px;
    }
  }
}

.confidence-badge {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.25rem 0.625rem;
  border-radius: 0.375rem;
  font-size: 0.75rem;
  margin-left: auto;

  &[data-level='high'] {
    background-color: rgba(34, 197, 94, 0.2);
    color: #bbf7d0;
  }

  &[data-level='medium'] {
    background-color: rgba(234, 179, 8, 0.2);
    color: #fef08a;
  }

  &[data-level='building'] {
    background-color: rgba(59, 130, 246, 0.2);
    color: #bfdbfe;
  }

  &[data-level='low'] {
    background-color: rgba(255, 255, 255, 0.1);
    color: rgba(255, 255, 255, 0.7);
  }

  .confidence-value {
    font-weight: 700;
    font-size: 0.875rem;
    line-height: 1;
  }

  .confidence-label {
    font-size: 0.5625rem;
    text-transform: uppercase;
    letter-spacing: 0.025em;
    opacity: 0.9;
  }
}

// Responsive adjustments
@media (max-width: 640px) {
  .header-content {
    gap: 0.5rem;
  }

  .header-title {
    font-size: 0.875rem;
  }

  .summary-items {
    gap: 0.5rem;
  }

  .summary-item {
    font-size: 0.6875rem;
    padding: 0.1875rem 0.5rem;
  }
}

// Panel styles (global via ::ng-deep since panels render outside component)
::ng-deep .header-edit-panel {
  .p-popover-content {
    padding: 0;
  }

  .panel-content {
    min-width: 280px;
  }

  .panel-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.75rem 1rem;
    border-bottom: 1px solid var(--surface-border);

    h4 {
      margin: 0;
      font-size: 0.875rem;
      font-weight: 600;
      color: var(--text-color);
    }

    .selection-count {
      font-size: 0.75rem;
      color: var(--text-color-secondary);
    }
  }

  .panel-body {
    padding: 0.75rem 1rem;
    max-height: 300px;
    overflow-y: auto;
  }

  .panel-footer {
    display: flex;
    justify-content: flex-end;
    gap: 0.5rem;
    padding: 0.75rem 1rem;
    border-top: 1px solid var(--surface-border);
  }

  // Vehicle list styles
  .vehicle-list {
    padding: 0.5rem;
  }

  .vehicle-item {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.5rem;
    border-radius: 0.375rem;
    cursor: pointer;
    transition: background-color 0.15s ease;

    &:hover {
      background-color: var(--surface-hover);
    }

    .vehicle-name {
      font-size: 0.875rem;
      color: var(--text-color);
      cursor: pointer;
      flex: 1;
    }
  }

  .empty-state {
    text-align: center;
    padding: 1rem;
    color: var(--text-color-secondary);
    font-size: 0.875rem;
  }

  // Date picker specific
  &.date-panel {
    .panel-body {
      padding: 0.5rem;
    }
  }

  // Data types panel specific
  &.data-types-panel {
    .data-types-list {
      display: flex;
      flex-direction: column;
      gap: 0.375rem;
      padding: 0.5rem;
    }

    .data-type-item {
      display: flex;
      align-items: flex-start;
      gap: 0.625rem;
      padding: 0.625rem;
      border-radius: 0.375rem;
      cursor: pointer;
      transition:
        background-color 0.15s ease,
        border-color 0.15s ease;
      border: 1.5px solid transparent;

      &:hover:not(.collecting) {
        background-color: var(--surface-hover);
      }

      &.selected:not(.collecting) {
        background-color: rgba(var(--color-primary-rgb, 59, 130, 246), 0.1);
        border-color: var(--color-primary-color, #3b82f6);
      }

      &.collecting {
        background-color: #fef3c7;
        border-color: #f59e0b;
        cursor: not-allowed;
        opacity: 0.9;

        .data-type-description {
          color: #b45309;
          font-style: italic;
        }
      }

      .collecting-icon {
        font-size: 1rem;
        color: #f59e0b;
        margin-top: 0.125rem;
      }

      .data-type-info {
        display: flex;
        flex-direction: column;
        gap: 0.125rem;
        flex: 1;
      }

      .data-type-name {
        display: flex;
        align-items: center;
        gap: 0.375rem;
        font-size: 0.875rem;
        font-weight: 500;
        color: var(--text-color);
        cursor: pointer;

        i {
          font-size: 0.875rem;
          color: var(--primary-color);
        }
      }

      .data-type-description {
        font-size: 0.75rem;
        color: var(--text-color-secondary);
        line-height: 1.3;
      }
    }
  }
}
